.calendar {
  width: 100%;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1px;
  background-color: #ddd;
}

.calendar-cell {
  background-color: white;
  min-height: 100px;
  padding: 8px;
}

.date {
  font-weight: bold;
  margin-bottom: 8px;
}

.calendar-item {
  padding: 4px 8px;
  margin-bottom: 4px;
  border-radius: 4px;
  font-size: 0.9em;
}

.status-backlog { background-color: #f8f9fa; }
.status-planned { background-color: #cce5ff; }
.status-in-progress { background-color: #fff3cd; }
.status-done { background-color: #d4edda; } 